import React, { Component } from 'react';
import * as d3 from "d3";
import * as webcola from "webcola";
import $ from "jquery"
import vis from "vis"


export default class TreeMap extends Component {
  constructor(props) {
    super(props);
    this.ref = React.createRef();

    this.state = {
      id: "salut"
    }
  }

  componentDidMount() {
    this.makeTree();

    this.drawTree(this.props.graph)

  }
  makeTree() {
    let node = this.ref.current;

    this.nodes = new vis.DataSet();
    this.edges = new vis.DataSet();
    var options = {
      layout: { randomSeed: 2 }, autoResize: true,
      height: '100%',
      width: '100%'
    };
    this.network = new vis.Network(node, { nodes: this.nodes, edges: this.edges }, options);

    this.network.on("click", (params) => {

      let id = this.network.getNodeAt(params.pointer.DOM);

      console.log(id, this.props)
      if (id && this.props.onClick) this.props.onClick(id)
      //console.log('click event, getNodeAt returns: ' + this.getNodeAt(params.pointer.DOM), params);
    });


  }

  drawTree(graph) {



    console.log("drawtree", graph)
    let h = $(this.ref.current).height();
    let w = $(this.ref.current).width();

    this.nodes.update(graph.nodes);
    this.edges.clear();
    this.edges.add(graph.edges);

  }


  componentWillReceiveProps(newprops) {
    console.log("will componentWillReceiveProps", newprops)
    this.drawTree(newprops.graph)
  }

  render() {
    return <div ref={this.ref} id={"#" + this.props.id} style={{ height: "100%", width: "100%" }}></div>
  }

}
